/*전문가, 카카오 스와이퍼*/
.live_pagination{font-size:13px; width:15%; position:absolute; top:30%; left:83%;}
.live_pagination span{padding:0 3px; display:inline-block;}
.sec_current_slide{font-weight:bold;}
.sec_current_slide:after{display: inline-block;
    content: '';
    width: 1px;
    height: 10px;
    background-color: #000;
    margin-left: 6px;}
.swiper_sec{margin-top:24px;}
.title{padding:0 15px; position:relative;}
.title .arrow_tit{display:flex; align-items:center;}
.title .arrow_tit{font-size:20px; font-weight:bold;}
.title .arrow_tit::after {
    content: '';
    display: inline-block;
    width: 6px;
    height: 6px;
    border: 2px solid #000;
    transform: rotate(45deg);
    border-width: 2px 2px 0 0;
    margin-left: 5px;
}
.swiper_sec .swiper_wrap{padding:10px 16px 10px 15px}
.swiper_sec .slide_list{height:250px; width:204px; border-radius:3px; overflow:hidden; box-shadow:0 3px 5px 0 rgba(0,0,0,0.1);}
.swiper_sec .slide_list > a{width:100%; display:inline-block;}
.swiper_sec .slide_list .cont_top{background-color:#edbeb8; margin:0; position:relative; text-align:center; height:134px; overflow:hidden;}
.swiper_sec .slide_list .cont_top > img{width:75%; position:absolute; left:50%; top:75%; transform:translate(-50%,-50%);}
.swiper_sec .slide_list .sec_cont_bottom{padding:10px; color:#000; background:#fff;}
.swiper_sec .slide_list .sec_cont_bottom .cont_expert_name{font-size:12px; font-weight:normal; color:inherit;}
.swiper_sec .slide_list .sec_cont_bottom .cont_expert_info{font-size:14px; font-weight:bold; height:40px; color:inherit;}
.swiper_sec .slide_list .sec_cont_bottom .hash_tag_wrap{margin-top:10px; color:#979797; font-size:11px; font-weight:normal;}
.swiper_sec .slide_list .sec_cont_bottom .hash_tag_wrap .hash_tag{display:inline-block;}
.swiper-slide.more{margin:0; width:120px; height:250px; background:#f6f7f9;}
.swiper-slide.more a{width:100%; height:100%; padding:100px 0; display:inline-block;}
.swiper-slide.more .more_ex{font-size:12px; display:block; color:#000; text-align:center;}
.swiper-slide.more .more_all{display:flex; justify-content:center; align-items:center; font-size:14px; font-weight:bold; color:#000;}
.swiper-slide.more .more_all:after{content: '';
    display: inline-block;
    width: 6px;
    height: 6px;
    border: 1px solid #000;
    transform: rotate(45deg);
    border-width: 1px 1px 0 0;
    margin-left: 2px;}

