/*이용후기*/
.review_wrap{margin-top:24px; width:100%; margin:19px 0 27px;}
.review_wrap .review_list{margin-top:11px;}
.review_wrap .review_all{display:flex; align-items:flex-start; justify-content:space-around;}
.review_wrap .review_item{width:43vw; border-radius:3px; box-shadow:0 3px 5px 0 rgba(0,0,0,0.1); background:#fff; padding:12px; margin-bottom:10px;}
.review_wrap .review_item:before{content: '';
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url(//img.x1.co.kr/x1/mobile/images/main/renewal/ic_quotes.png) no-repeat center;
    background-size: auto;
    margin-bottom: 5px;}
.review_wrap .review_item a{display:inline-block; width:100%;}
.review_wrap .review_item a .review_item_top{padding-bottom:12.5px; border-bottom:.5px dashed rgba(0,0,0,0.2);}
.review_wrap .review_item a .review_item_top .review_img{width:100%; text-align:center; position:relative; max-height:90px; overflow:hidden;}
.review_wrap .review_item a .review_item_top .cert_ok::before{content: '';
    display: inline-block;
    width: 45px;
    height: 19px;
    background: url(//img.x1.co.kr/x1/mobile/images/main/renewal/ic_certifi.svg) no-repeat center;
    background-size: cover;
    position: absolute;
    left: 3px;
    top: 3px;
    z-index: 99;}
.review_wrap .review_item a .review_item_top img{width:100%; text-align:center; position:relative; max-height:90px; overflow:hidden;}
.review_wrap .review_item a .review_item_top img{width:100%;}
.review_wrap .review_item a .review_item_top .review_text{margin-top: 9px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: box;
    font-size: 14px;
    font-weight: bold;
    color: #000;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-all;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;}
.review_wrap .review_item a .review_item_bottom{margin-top:12.5px; width:100%;}
.review_wrap .review_item a .review_item_bottom .review_user_name{display:block; font-size:14px; color:#000;}
.review_wrap .review_item a .review_item_bottom .review_user_id{display:inline-block; font-size:11px; color:979797;}

