/*네비-슬라이드*/
.tab_floating_wrap{background:#f0f0f0; height:45px; overflow:hidden;}
.slide_nav{position:relative; padding:0 15px; border-bottom:1px solid #e6e6e6; color:#666;}
.slide_nav .swiper-slide{display:inline-block; width:initial; position:relative; height:35px; line-height:35px; color:#a9a6b0;}
.slide_nav .swiper-slide.active{color:#3a3659; font-weight:bold;}
.slide_nav .swiper-slide.active:after{content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    border-bottom: 2px solid #3A3659;
    line-height: 35px;}